.hero{

    height: 89vh;
    background: linear-gradient(180deg,#F0F0F0,#e1ffea22 60%);
    display: flex;
    /* position: relative;
    z-index:-1;  */
    margin-bottom: 50px;
    /* border: 2px solid rebeccapurple; */

}

.hero-left{

    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;

}
.hero-left h2{

    color: #333333;
    font-size: 26px;
    font-weight: 600;

}

.hero-left p{

    color: #333333;
    font-size: 90px;
    font-weight: 700;

}

.hero-hand-icon{

    display: flex;
    align-items: center;
    gap: 20px;

}

.hero-hand-icon img{

    width: 105px;


}
.hero-latest-btn{

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #FFD700;
    color: #333333;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;


}
.hero-latest-btn:hover{

    background: #333333;
    color: #FFD700;
}
.hero-right{

    /* border: 1px solid firebrick; */
    height: 100%;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    

}
.hero-right img{

    height: 613px;
    /* height: 100%; */



}

@media (max-width:1280px){

   .hero-left{

        padding-left: 100px;

   }
   .hero-left h2{

    font-size: 22px;
   }
   .hero-left p{
    font-size: 60px;
   }
   .hero-hand-icon{

    width: 80px;
   }
   .hero-latest-btn{

    gap: 10px;
    width: 250px;
    height: 60px;
    margin-top: 20px;
    font-size: 18px;


   }
   .hero-right img{

    width: 500px;
   }
}
@media (max-width:1024px){

    .hero-left{
 
         padding-left: 80px;
 
    }
    .hero-left h2{
 
     font-size: 20px;
    }
    .hero-left p{
     font-size: 50px;
    }
    .hero-hand-icon{
 
     width: 65px;
    }
    .hero-latest-btn{
 
     
     width: 220px;
     height: 50px;
     
     font-size: 1rem;
 
 
    }
    .hero-right img{
 
     width: 400px;
    }
 }
 @media (max-width:800px){

    .hero{

        position: relative;
        z-index:-1; 
    }
    .hero-left{
 
         padding-left: 30px;
 
    }
    .hero-left h2{
 
     font-size: 16px;
    }
    .hero-left p{
     font-size: 40px;
    }
    .hero-hand-icon{
 
     width: 50px;
    }
    .hero-latest-btn{
 
     
     width: 175px;
     height: 40px;
     
     font-size: 13px;
 
 
    }
    .hero-right img{
 
     width: 300px;
     object-fit: contain;
    }
 }
 @media (max-width:500px){

    .hero{

        flex-direction: column;
        height: 81vh;
        position: relative;
    z-index:-1; 
        /* background: #333333; */
    }
    .hero-right{

        display: none;
    }
    .hero-left h2{

        font-size: 18px;

    }
    .hero-left p{

        font-size: 50px;
    }
    .hero-hand-icon img{

        width: 55px;
    }
    .hero-latest-btn{

        width: 200px;
        height: 50px;
        font-size: 14px;
    }

 }
 