.loginsignup{

    width: 100%;
    height: 90vh;
    background: linear-gradient(180deg,#F0F0F0,#e1ffea22 60%);

    /* padding-top: 47px; */
    display: flex;
    justify-content: center;
    align-items: center;
    
    
}
.loginsignup-container{

    width: 590px;
    height: auto;

    background: white;
    margin: auto;
    padding: 0px 60px;
    border-radius: 10px;
    box-shadow: 1px 1px 2px  #333333;
    /* border: 0.5px solid  #333333; */


}
.loginsignup-container h1{

    margin: 20px 0;

}
.loginsignup-fields{

    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;

}
.loginsignup-fields input{

    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;

}
.loginsignup-container button{

    width: 100%;
    height: 72px;
    color: #333333;
    background: #FFD700;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;


}
.loginsignup-container button:hover{

    background: #333333;
    color: #FFD700;
}
.loginsignup-login{

    margin-top: 20px;
    color: #5c5c5c;
    font-size: 1rem;
    font-weight: 500;

}
.loginsignup-login span{

    color: #FFD700;
    font-weight: 600;

}
.loginsignup-agree{

    display: flex;
    align-items: center;
    margin-top: 25px;
    margin-bottom: 20px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 1rem;
    font-weight: 500;
}
.loginsignup-login span{

    cursor: pointer;
}

@media (max-width:1280px){

    .loginsignup{

        padding-top: 50px;
    }
    .loginsignup-container{

        width: auto;
        max-width: 500px;
        max-height: 550px;
    }
    .loginsignup-container h1{

        margin: 10px 0px;

    }
    .loginsignup-fields{

        gap: 20px;
        margin-top: 20px;
    }
    .loginsignup-fields input{

        height: 65px;
        width: 100%;
    }
    .loginsignup-container button{

         width: 100%;
    }
    .loginsignup-login{

        font-size: 16px;
    }
    .loginsignup-agree{

        font-size: 14px;
        gap: 10px;
    }
}

@media (max-width:800px){

    .loginsignup-container{

        padding: 20px 30px;
        max-height: 500px;
    }
    .loginsignup-container h1{

        font-size: 24px;
    }
    .loginsignup-fields{

        margin-top: 20px;
    }
    .loginsignup-fields input{

        height: 50px;
    }
    .loginsignup-container button{

        height: 50px;
        font-size: 16px;
    }
}